import React from "react";

class Image extends React.Component<{ el: Element }> {
  // TODO: placeholder
  render() {
    return <img src={this.props.el.getAttribute("src") || ""} />;
  }
}

export function createHTMLComponent(
  node: Element,
  children: React.ReactElement[]
): React.ReactElement {
  console.log("node.nodeName", node.nodeName);
  if (node.nodeName === "img") {
    return <Image el={node} />;
  }

  return React.createElement(node.nodeName, null, ...children);
}
